@media (min-width: 500px) and (min-resolution: 2dppx) {
  .header {
    background: url("../img/intro/intro-tab@2x.jpg") center/cover no-repeat;
  }
}
@media (max-width: 499px) {
  .header {
    background: url("../img/intro/intro-mob.jpg") center/cover no-repeat;
  }
}
@media (max-width: 499px) and (min-resolution: 2dppx) {
  .header {
    background: url("../img/intro/intro-mob@2x.jpg") center/cover no-repeat;
  }
}

.header .container {
  display: flex;
  justify-content: space-between;
}

.header__logo-icon {
  width: 70px;
  height: 38px;
  fill: #f9fbfd;
}
@media (max-width: 1023px) {
  .header__logo-icon {
    width: 38px;
    height: 20px;
  }
}
@media (max-width: 767px) {
  .header__logo-icon {
    width: 42px;
    height: 32px;
  }
}

@media (max-width: 767px) {
  .is-open .header__logo-icon {
    fill: #011C40;
  }
}

@media (max-width: 767px) {
  .is-open .header__link-logo {
    display: block;
    width: 42px;
    height: 32px;
    z-index: 7;
    position: relative;
    right: -12%;
    transform: translateY(20px);
  }
  .is-open .header__link-logo::after {
    position: absolute;
    content: "";
    height: 70px;
    min-width: 240px;
    background-color: #f9fbfd;
    top: -30px;
    left: -15px;
    z-index: -10;
  }
}

@media (max-width: 767px) {
  .is-open .header__container {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 100;
    width: 100%;
    background-color: rgba(0, 43, 102, 0.5);
    overflow-x: hidden;
  }
}

.nav__list {
  list-style-type: none;
  padding: 0;
  display: flex;
  gap: 33px;
  margin: 0;
}
@media (max-width: 767px) {
  .nav__list {
    flex-direction: column;
  }
}

.nav a {
  color: #f9fbfd;
  font-weight: 500;
  line-height: 20px;
}
@media (max-width: 1023px) {
  .nav a {
    font-size: 14px;
    line-height: 17px;
  }
}
@media (max-width: 767px) {
  .nav a {
    font-size: 18px;
    line-height: 22px;
  }
}

@media (max-width: 767px) {
  .is-open a {
    color: #011C40;
  }
}

.nav__toggle {
  display: none;
}
@media (max-width: 767px) {
  .nav__toggle {
    display: block;
    position: relative;
    top: 12px;
    width: 30px;
    height: 30px;
    z-index: 6;
    cursor: pointer;
    border: none;
    background-color: transparent;
  }
}

@media (max-width: 767px) {
  .is-close .nav__toggle::before {
    content: "";
    position: absolute;
    top: 6px;
    left: 3px;
    width: 24px;
    height: 2px;
    background-color: #f9fbfd;
    box-shadow: 0 5px 0 0 #f9fbfd, 0 10px 0 0 #f9fbfd;
  }
}

@media (max-width: 767px) {
  .is-open .nav__toggle::before, .is-open .nav__toggle::after {
    content: "";
    position: absolute;
    top: 12px;
    left: 8px;
    width: 13px;
    height: 2px;
    background-color: #011C40;
    transform: rotate(0);
    transition: transform 0.2s ease-in;
  }
  .is-open .nav__toggle::before {
    transform: rotate(45deg);
  }
  .is-open .nav__toggle::after {
    transform: rotate(-45deg);
  }
}

@media (max-width: 767px) {
  .is-close .nav__list {
    display: none;
  }
}

@media (max-width: 767px) {
  .is-open .nav__list {
    position: absolute;
    top: 0;
    right: 0;
    padding: 75px 35px 30px 15px;
    height: 100%;
    width: 89%;
    text-align: start;
    background-color: #f9fbfd;
    overflow-y: scroll;
    overflow-x: hidden;
  }
}

