.catalog__list {
  list-style-type: none;
  padding: 0;
  margin: 0;
  color: #f9fbfd;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(260px, 1fr));
  grid-auto-rows: 386px;
  grid-column-gap: 2.8%;
  grid-row-gap: 27px;
}
@media (max-width: 767px) {
