  color: #d8d8d9;
}

.catalog__item img {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  object-fit: cover;
  display: block;
  z-index: 30;
}

.catalog__item:hover img {
  display: none;
  z-index: -1;
}

.catalog__item a:focus + picture {
  display: none;
}

.catalog__content {
  overflow-y: auto;
  margin-bottom: 30px;
}

