.is-open .header__container {
  @include vp-767 {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 100;
    width: 100%;
    background-color: rgba(0, 43, 102, 0.5);
    overflow-x: hidden;
  }
}
